body {
  margin: 0;
  padding: 0;
  font-family: "Lora", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  margin: 0;
  text-align: center;
}

h2 {
  font-family: 'Open Sans', sans-serif;
  font-size: 40px;
  font-weight: normal;
  letter-spacing: 1px;
  text-transform: lowercase;
  margin: 0;
  text-align: center;
}

h3 {
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin: 25px 0;
}

h4 {
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  font-size: 15px;
  letter-spacing: 1px;
}

img {
  margin-bottom: 10px;
}

p {
  line-height: 2;
  text-align: justify;
  font-size: 20px;
}

ol {
  font-size: 18px;
}

ol li {
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
  margin-top: 10px;
}

ol li:last-child {
  border-bottom: 0;
}

.navbar {
  z-index: 999;
  display: flex;
  justify-content: flex-end;
}

#navIcon {
  background-color: #fff;
  height: 50px;
  width: 50px;
  top: 50px;
  right: 50px;
  position: fixed;
  border: 2px solid black;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  -webkit-transition: .3s;
  transition: .3s;
}

#navIcon:hover {
  cursor: pointer;
  background-color: #000;
  color: #fff;
}

#navIcon img {
  margin-bottom: 0;
}

.nav {
  justify-content: space-around;
  width: 100%;
  max-height: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;
  height: 0;
  -webkit-transition: height .5s ease-out;
  transition: height .5s ease-out;
  position: fixed;
  z-index: 99;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.nav.open {
  max-height: 100vh;
  height: 100vh;
  margin-top: 0;
  -webkit-transition: height .5s ease-out;
  transition: height .5s ease-out;
}

ul.nav li {
  font-family: 'Lora', serif;
  font-size: 60px;
  list-style-type: none;
  margin: 10px 30px;
}

ul.nav li a {
  color: white;
  text-decoration: none;
  -webkit-transition: .3s;
  transition: .3s;
  border-bottom: 1px solid black;
}

ul.nav li a:hover {
  border-bottom: 1px solid white;
}

@-webkit-keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 10px;
  }
}

@keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 10px;
  }
}

#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -2;
}

.overlay {
  position: relative;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

#introP {
  font-size: 16px;
}

.content {
  width: 300px;
  color: white;
  margin-top: 150px;
  border: 2px solid white;
  padding: 50px;
}

img#arrow {
  -webkit-animation: MoveUpDown 3s linear infinite;
          animation: MoveUpDown 3s linear infinite;
  width: 50px;
  height: 50px;
  bottom: 0;
  position: absolute;
}

img#arrow:hover {
  cursor: pointer;
}

.botBorder {
  border: 3px solid white;
  width: 80px;
  margin-left: 107px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.botBorder-black {
  border: 3px solid black;
  width: 80px;
  margin-top: 30px;
  margin-bottom: 30px;
}

